.Banner {
  background-color: rgba(0, 0, 0, 0.5);
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url("/static/images/banner-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
  position: relative; }
  @media screen and (max-width: 1280px) {
    .Banner {
      height: 800px; } }
  @media screen and (max-width: 960px) {
    .Banner {
      height: 600px; } }
  @media screen and (max-width: 600px) {
    .Banner {
      height: 450px; } }

.Title {
  margin: 0;
  padding: 0;
  position: absolute;
  top: 300px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  letter-spacing: 3px;
  color: #fff;
  text-transform: uppercase;
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  font-size: 60px;
  width: 65%;
  line-height: 1.3; }
  @media screen and (max-width: 1280px) {
    .Title {
      top: 250px;
      font-size: 50px; } }
  @media screen and (max-width: 960px) {
    .Title {
      font-size: 35px; } }
  @media screen and (max-width: 600px) {
    .Title {
      font-size: 22px;
      width: 90%; } }
