.TopLine {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background-color: #1e2d3b;
  padding: 10px 0; }
  @media screen and (min-width: 960px) {
    .TopLine {
      background-color: #edcd1f;
      padding: 10px 20px; } }

.Container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  box-sizing: border-box;
  display: flex;
  justify-content: flex-end;
  align-items: center; }
  @media (min-width: 600px) {
    .Container {
      min-width: 560px; } }
  @media (min-width: 960px) {
    .Container {
      min-width: 900px; } }
  @media (min-width: 1280px) {
    .Container {
      min-width: 1200px; } }
  @media (min-width: 1920px) {
    .Container {
      min-width: 1800px; } }
  @media (min-width: 2400px) {
    .Container {
      min-width: 2300px; } }
