.Title {
  margin: 50px 0; }

.Cards {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center; }
  @media (min-width: 600px) {
    .Cards {
      min-width: 560px; } }
  @media (min-width: 960px) {
    .Cards {
      min-width: 900px; } }
  @media (min-width: 1280px) {
    .Cards {
      min-width: 1200px; } }
  @media (min-width: 1920px) {
    .Cards {
      min-width: 1800px; } }
  @media (min-width: 2400px) {
    .Cards {
      min-width: 2300px; } }
  @media screen and (max-width: 960px) {
    .Cards {
      flex-direction: column; } }

.Card {
  margin: 60px 30px; }
