.Card {
  overflow: hidden;
  width: 350px; }

.ImageBox {
  width: 100%;
  height: 250px; }

.Image {
  max-width: 100%; }

.Content {
  padding: 0 20px;
  font-family: "PT Sans", sans-serif;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.1); }

.Label {
  color: #3d5975;
  font-family: inherit;
  text-align: center;
  text-transform: capitalize;
  font-size: 24px;
  letter-spacing: 2px;
  padding: 25px 0;
  margin: 0;
  font-weight: 500; }

.Context {
  font-family: inherit;
  color: #3B3A3A;
  font-size: 18px; }
  @media screen and (max-width: 600px) {
    .Context {
      font-size: 16px; } }

.Context::first-letter {
  padding-left: 25px; }

.Price {
  font-family: inherit;
  font-size: 35px;
  font-weight: 700;
  text-align: center;
  color: #edcd1f;
  background-color: #1e2d3b;
  padding: 15px 0;
  margin: 50px -20px 0 -20px; }
  @media screen and (max-width: 600px) {
    .Price {
      font-size: 30px; } }
