.Achievement {
  background-color: #edcd1f;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  box-sizing: border-box;
  padding: 70px 0;
  display: flex;
  justify-content: center; }
  @media (min-width: 600px) {
    .Achievement {
      min-width: 560px; } }
  @media (min-width: 960px) {
    .Achievement {
      min-width: 900px; } }
  @media (min-width: 1280px) {
    .Achievement {
      min-width: 1200px; } }
  @media (min-width: 1920px) {
    .Achievement {
      min-width: 1800px; } }
  @media (min-width: 2400px) {
    .Achievement {
      min-width: 2300px; } }
  @media screen and (max-width: 960px) {
    .Achievement {
      flex-direction: column; } }

.Item {
  font-family: "PT Sans", sans-serif;
  display: inline-block;
  padding: 0 40px;
  text-align: center;
  border-right: 1px solid #fff;
  border-left: 1px solid #fff; }
  @media screen and (max-width: 960px) {
    .Item {
      padding-top: 30px; } }

.Title {
  font-size: 60px;
  color: #fff;
  font-family: inherit;
  white-space: nowrap;
  margin: 0;
  padding: 0; }
  @media screen and (max-width: 960px) {
    .Title {
      font-size: 50px; } }

.Text {
  font-size: 22px;
  color: #3d5975;
  font-family: inherit;
  text-transform: unset;
  margin: 0;
  padding: 0; }
  @media screen and (max-width: 960px) {
    .Text {
      font-size: 20px; } }
