.Link {
  font-family: "PT Sans", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  color: #fff;
  cursor: pointer;
  letter-spacing: 1px;
  display: block;
  box-sizing: border-box;
  padding: 17px 20px;
  transition: color 0.3s; }
  .Link_active, .Link:hover {
    color: #edcd1f; }
  .Link_view_murky {
    color: #3B3A3A; }
  .Link_view_murky:not(:last-child) {
    border-bottom: 2px solid #fff; }
  .Link_view_murky.Link_active, .Link_view_murky:hover {
    color: #fff; }
  @media screen and (max-width: 960px) {
    .Link {
      width: 250px; } }
