.Navbar {
  width: 100%;
  background-color: #1e2d3b;
  position: fixed;
  top: 40px;
  left: 0;
  right: 0;
  display: block;
  margin: 0 auto;
  z-index: 1000; }
  @media screen and (max-width: 960px) {
    .Navbar {
      background-color: #edcd1f; }
      .Navbar__Backdrop {
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh;
        width: 100vw;
        background-color: #edcd1f; }
      .Navbar__HamburgerIcon {
        position: absolute;
        top: 20px;
        right: 20px;
        z-index: 1200; }
      .Navbar__Navigation {
        padding-top: 120px; } }

.Container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  box-sizing: border-box;
  height: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  @media (min-width: 600px) {
    .Container {
      min-width: 560px; } }
  @media (min-width: 960px) {
    .Container {
      min-width: 900px; } }
  @media (min-width: 1280px) {
    .Container {
      min-width: 1200px; } }
  @media (min-width: 1920px) {
    .Container {
      min-width: 1800px; } }
  @media (min-width: 2400px) {
    .Container {
      min-width: 2300px; } }

.LogoBox {
  background-color: #edcd1f;
  width: 170px;
  max-width: 170px;
  height: 100%;
  margin-left: 50px;
  display: flex;
  justify-content: center;
  align-items: center; }
  @media screen and (max-width: 960px) {
    .LogoBox {
      margin-left: 0px; } }

.FadeOut-enter {
  transform: translateY(-100%); }

.FadeOut-enter-active {
  transform: translateY(0%);
  transition: all 0.4s ease-out; }

.FadeOut-exit-active {
  transform: translateY(-100%);
  transition: all 0.4s ease-out; }
