.Container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.Title {
  font-family: "PT Sans", sans-serif;
  font-size: 50px;
  text-align: center;
  color: #c6c6c6;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin: 0;
  padding: 20px 0;
  line-height: 2; }
  @media screen and (max-width: 960px) {
    .Title {
      font-size: 35px; } }

.Line {
  margin: 0 auto;
  width: 60%;
  height: 8px;
  background-color: #edcd1f; }
  @media screen and (max-width: 960px) {
    .Line {
      height: 6px; } }

.Line_left {
  margin: 0; }
